.joke {
    display: flex;
    padding: 10px;
    border-bottom: 1px solid black;
    align-items: center;
  
    &__actions {
      display: flex;
      align-items: center;
    }

    &__button {
      border: none;
      background-color: transparent;
      padding: 10px;

      &--up {

        &:hover {
          color: green;
          animation: arrow-up 0.8s ease-in-out 2;
        }
      }

      &--down {

        &:hover {
          color: red;
          animation: arrow-down 0.8s ease-in-out 2;
        }
      }
    }
  
    &__text {
      padding: 0 10px;
      margin: 0;
    }
  
    &__smile {
      font-size: 30px;
      margin-left: auto;
    }
  
    &__status {
      width: 70px;
      padding: 5px 5px;
      border: 2px solid green;
      border-radius: 15px;
      background-color: transparent;
      transition: 0.7s;
      font-size: 16px;
      font-family: 'Big Shoulders Text', cursive;

      &:focus {
        outline: none;
        box-shadow: 0px 0px 7px 0px green;
      }
      
      &:hover {
        box-shadow: 0px 0px 7px 0px green;
      }

      &--saved {
        background-color: green;
      }
    }
  
    &__rating {
      display: flex;
      align-items: center;
      padding: 0 15px;
    }
  
    &__test {
      margin: 0;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 3px solid black;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 10px;
    }

    &__votes {
      color: black;
      font-size: 18px;
    }

    &__checkmark {
      display: inline-block;
      width: 9px;
      height: 16px;
      border-right: 2px solid white;
      border-bottom: 2px solid white;
      transform: rotate(45deg)
    }
  }