html {
	box-sizing: border-box;
}

*, *::before, *::after {
	box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Big Shoulders Text', cursive;
}

// анимация
@keyframes arrow-up {
  0% {transform: translateY(0)}
  50% {transform: translateY(-5px)}
  100% {transform: translateY(0)}
}

@keyframes arrow-down {
  0% {transform: translateY(0)}
  50% {transform: translateY(5px)}
  100% {transform: translateY(0)}
}